function PostsMessages(props) {

    const classes = useStyles()

    return (
        <List className={classes.root}>
            <ListItem className={classes.postList}>
                <Typography>
                    {props.message}
                </Typography>
                <div>
                    <Button className={classes.buttons} size="small" color="primary" variant="contained">
                    <span>
                        <ThumbUpAltIcon />
                        <strong>{props.likes}</strong>
                    </span>
                    </Button>
                    <Button size="small" color="secondary" variant="contained">
                        <span>
                            <ThumbDownIcon fontSize="small" />
                        </span>
                    </Button>
                </div>

            </ListItem>
        </List>

    );
}

export default PostsMessages;