import React from "react";
import '../components/Navbar/nav.css'
import useStyles from "../styles/postsStyles"
import {Container, Grid} from "@material-ui/core";
import PostsMessages from "../components/Posts/PostsMessages";
import PostsForm from "../components/Posts/PostsForm";


const Posts = (props) => {
    const classes = useStyles()

    return (
        <div className={classes.root}>
            <Container className={classes.container}>
                <h1 className={classes.title}>Posts Page</h1>
                <Grid container>
                    <Grid item xs={12}>
                        <div className={classes.postContainer}>
                            <PostsForm  addPost={props.addPost} />
                            <div className={classes.postsMessages}>
                                { [...props.posts].reverse().map((el, idx) => (
                                    <PostsMessages key={idx} id={el.id} message={el.message} likes={el.likes}/>))}
                            </div>
                        </div>
                    </Grid>
                </Grid>

            </Container>
        </div>
    )
}


export default Posts