import React from 'react'
import {Button, TextField} from "@material-ui/core";
import SendIcon from "@material-ui/icons/Send";
import {Field, reduxForm} from "redux-form";


const ReduxPostForm = ({handleSubmit}) => {

    const renderPostField = ({
                                 label,
                                 input,
                                 meta: { touched, invalid, error },
                                 ...custom
                             }) => (
        <TextField
            label={label}
            error={touched && invalid}
            helperText={touched && error}
            {...input}
            {...custom}
        />
    )
    return (
        <div>
            <form onSubmit={handleSubmit}>
                <Field
                    autoComplete="on"
                    fullWidth={true}
                    component={renderPostField}
                    name={'post'}
                    label={'send post'}
                />
                <Button fullWidth={true} color="primary" type={"submit"} variant="contained">
                    Send
                    &nbsp;
                    <SendIcon/>
                </Button>
            </form>
        </div>
    )
}

const ReduxPostFormConnect = reduxForm({
    form: 'post-form'
})(ReduxPostForm)

export default ReduxPostFormConnect