import React from 'react';
import ReduxPostFormConnect from "./ReduxPostForm";


const PostsForm = ({addPost}) => {

    const addPostSubmit = (formData) => {
        addPost(formData.post)
        formData.post = ''
    }

    return (
        <ReduxPostFormConnect onSubmit={addPostSubmit} />
    )
}

export default PostsForm